import { ImageResponse } from 'next/og';
import { supabase } from "@/lib/supabase";
import QRCode from 'qrcode';

export const runtime = 'nodejs';

export async function GET(
  request: Request,
  { params }: { params: Promise<{ slug: string }> }
) {
  const { slug } = await params;
  const storeUrl = `https://storelink.ng/s/${slug}`;

  // 1. FETCH STORE DATA (The "Store Identity")
  const { data: store, error: storeError } = await supabase
    .from('stores')
    .select('id, name, location, slug, description')
    .eq('slug', slug)
    .single();

  if (storeError || !store) return new Response("Store not found", { status: 404 });

  // 2. QR ENGINE (The "Entry Point")
  const qrCodeSvg = await QRCode.toString(storeUrl, {
    type: 'svg',
    width: 500,
    margin: 1,
    color: { dark: '#111827', light: '#ffffff' },
  });
  const qrCodeDataUri = `data:image/svg+xml;base64,${Buffer.from(qrCodeSvg).toString('base64')}`;

  // 3. THE BRAND MASTERPIECE (A4 PORTRAIT)
  return new ImageResponse(
    (
      <div style={{ height: '100%', width: '100%', display: 'flex', flexDirection: 'column', backgroundColor: '#ffffff' }}>
        
        {/* TOP: THE EMPIRE HEADER (70% of the flyer) */}
        <div style={{ display: 'flex', flexDirection: 'column', backgroundColor: '#111827', padding: '120px 100px', flex: 1, position: 'relative', overflow: 'hidden' }}>
          
          {/* Decorative Brand Accent */}
          <div style={{ position: 'absolute', top: '-10%', right: '-10%', width: '1000px', height: '1000px', borderRadius: '50%', background: 'radial-gradient(circle, rgba(16,185,129,0.1) 0%, rgba(0,0,0,0) 70%)', display: 'flex' }} />

          {/* Nav Logo Branding */}
          <div style={{ display: 'flex', alignItems: 'center', marginBottom: '100px' }}>
            <div style={{ display: 'flex', color: '#10b981', marginRight: '20px' }}>
              <svg width="80" height="80" viewBox="0 0 24 24" fill="none" stroke="currentColor" stroke-width="2.5" stroke-linecap="round" stroke-linejoin="round">
                <rect width="7" height="9" x="3" y="3" rx="1" /><rect width="7" height="5" x="14" y="3" rx="1" /><rect width="7" height="9" x="14" y="12" rx="1" /><rect width="7" height="5" x="3" y="16" rx="1" />
              </svg>
            </div>
            <span style={{ display: 'flex', fontSize: '56px', fontWeight: '900', color: '#ffffff', letterSpacing: '-3px' }}>StoreLink</span>
          </div>

          {/* THE STORE NAME (The Hero) */}
          <h1 style={{ display: 'flex', fontSize: '200px', fontWeight: '900', color: '#ffffff', textTransform: 'uppercase', fontStyle: 'italic', lineHeight: '0.8', margin: 0, letterSpacing: '-10px' }}>
            {store.name}
          </h1>

          <div style={{ display: 'flex', alignItems: 'center', marginTop: '60px' }}>
            <div style={{ display: 'flex', width: '100px', height: '10px', backgroundColor: '#10b981', marginRight: '30px' }} />
            <span style={{ display: 'flex', fontSize: '50px', fontWeight: '700', color: '#9ca3af', textTransform: 'uppercase' }}>
              {store.location || "LAGOS, NIGERIA"}
            </span>
          </div>

          {/* Store Description / Slogan */}
          <div style={{ display: 'flex', marginTop: '100px', maxWidth: '800px' }}>
            <span style={{ display: 'flex', fontSize: '42px', fontWeight: '600', color: '#d1d5db', lineHeight: '1.4' }}>
              {store.description || "Your favorite store is now online. Experience seamless shopping and instant WhatsApp ordering."}
            </span>
          </div>

          {/* Trust Markers */}
          <div style={{ display: 'flex', gap: '40px', marginTop: 'auto' }}>
            <div style={{ display: 'flex', backgroundColor: 'rgba(255,255,255,0.05)', padding: '20px 40px', borderRadius: '30px', border: '1px solid rgba(16,185,129,0.3)' }}>
              <span style={{ color: '#10b981', fontSize: '24px', fontWeight: '900' }}>✓ SECURE SHOPPING</span>
            </div>
            <div style={{ display: 'flex', backgroundColor: 'rgba(255,255,255,0.05)', padding: '20px 40px', borderRadius: '30px', border: '1px solid rgba(16,185,129,0.3)' }}>
              <span style={{ color: '#10b981', fontSize: '24px', fontWeight: '900' }}>✓ WHATSAPP READY</span>
            </div>
          </div>
        </div>

        {/* BOTTOM: THE CONVERSION ZONE */}
        <div style={{ display: 'flex', padding: '100px', alignItems: 'center', justifyContent: 'space-between', backgroundColor: '#ffffff', borderTop: '15px solid #10b981' }}>
          <div style={{ display: 'flex', flexDirection: 'column', flex: 1 }}>
            <span style={{ display: 'flex', fontSize: '90px', fontWeight: '900', color: '#111827', lineHeight: '1' }}>SCAN TO</span>
            <span style={{ display: 'flex', fontSize: '90px', fontWeight: '900', color: '#10b981', lineHeight: '1', marginBottom: '40px' }}>VISIT STORE</span>
            
            <div style={{ display: 'flex', backgroundColor: '#f3f4f6', padding: '30px 50px', borderRadius: '40px', alignSelf: 'flex-start' }}>
                <span style={{ color: '#111827', fontSize: '42px', fontWeight: '900' }}>storelink.ng/s/{slug}</span>
            </div>
          </div>
          
          <div style={{ display: 'flex', padding: '40px', backgroundColor: '#ffffff', borderRadius: '70px', border: '15px solid #111827' }}>
             <img src={qrCodeDataUri} width="400" height="400" alt="QR Code" />
          </div>
        </div>

        <div style={{ display: 'flex', justifyContent: 'center', paddingBottom: '40px', backgroundColor: '#ffffff' }}>
           <span style={{ fontSize: '20px', fontWeight: '800', color: '#d1d5db', letterSpacing: '5px' }}>OFFICIAL STORELINK VENDOR</span>
        </div>
      </div>
    ),
    { width: 1240, height: 1754 }
  );
}